import React, { useRef, useEffect } from "react";

const AudioPlayer = () => {
  const audioRef = useRef(null);

  useEffect(() => {
    const audio = audioRef.current;
    audio.play();
    audio.loop = true;

    console.log(audio);
  }, []);

  return (
    <audio ref={audioRef} autoPlay>
      {/* <source src={require("../assets/audio/bgm.mp3")} /> */}
      <source
        src="../assets/audio/7th Floor Tango - Silent Partner.mp3"
        type="audio/mp3"
      />
    </audio>
  );
};

export default AudioPlayer;
